@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap');

body {
  font-family: "Roboto Condesed",sans-serif !important;
  width: 100% !important;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


/* menu link */

@media(max-width: 468px)
{
  .navbar {
    width: 44.6%;
  }
}


.navbar {
  background-color: rgb(34, 33, 33);
  z-index: 2;
  width: 100% !important;
  position: fixed !important;
}


.navbar-toggler {
  z-index: 1;
}

.nav {
  z-index: 1;
}

nav a.nav-link {
  letter-spacing: 0.6px;
  cursor: pointer;
  z-index: 1;

}

nav ul li {
  text-transform: uppercase;
  z-index: 1;
}


.navbar-nav > li > a {
  margin-right: 12px;
  margin-left: 12px;
  color: #fff !important;
  border-top: 0.1875rem solid rgba(255, 255, 255, 0);
  z-index: 1;
}

.navbar-nav >li > a.active, .navbar-nav >li> a:focus {
  border-top: 0.1875rem solid #5DADEC;
  z-index: 1;
}

.navbar-nav >li> a:hover {
  border-top: 0.1875rem solid #5DADEC;
  z-index: 1;
} 



/* Header */

.header-wrapper {
  position: relative;
  background: url(nxg.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
}

.main-info {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;

  z-index: 1;
}

.main-info h1 {
  color: #7fc9d6;
  text-transform: none;
  text-align: justify;
  
  bottom: 200px;
}
@media(max-width:728px)
{
  .main-info h1 {
    font-size: 1.75rem !important;
  }
}

/* Typed text */

.typed-text {
  font-size: 2rem;
  color: #fff;
  
}

@media(max-width: 728px)
{
  .typed-text {
    font-size: 1.3rem;
  }
}

.btn-main-offer {
  border: 1px solid #ef4035;
  text-transform: uppercase;
  font-family: sans-serif;
  border-radius: 8px;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  color: white;
  margin-top: 3.5rem;
}

.btn-main-offer:hover {
  text-decoration: none;
  background-color: orange;
  transition: .2s ease-in-out;
}

/* Particle */

.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vh;
  z-index: 1;
}




/* About me */

.con {
  margin-bottom: 2rem;
  margin-top: 2rem;
  margin-left: 2rem;
  margin-right: 2rem;
  background: #817a7a;
  border: .5px solid rgb(48, 41, 41); 
  filter: drop-shadow(2px 4px 8px #585858);
}

.photo-wrap {
  display: flex;
  justify-content: center;
  align-items: center;

}

.profile {
  width: 18rem;
  margin-top: 2.5rem;
  border-radius: 2rem;
  padding: .3rem;
  border: 2px solid #043458;
}

.he1 {
  color: #062c49;
  text-transform: uppercase;
  padding: .3rem;
}

.info {
  font-size: 1.36rem;
  padding: .3rem;
  color: rgb(12, 10, 10);
  text-decoration: none;
  font-family: Verdana, Geneva, Tahoma, sans-serif !important;
}

@media(max-width: 468px) {
  .profile {
    width: 10rem;

  }
  .info {
    font-size: 1.26rem;
    -webkit-line-clamp: 3;
  }
}




/* Services */

.services {
  text-align: center;
  padding-bottom: 2rem;
  background: #817a7a;
  margin-top: 3rem;
  
}

.services h1 {
  color: rgb(10, 10, 10);
  text-transform: uppercase;
  font-family: italic, bold;
}

.services .circle {
  position: relative;
  margin: 0.375rem auto;
  background: #5d89aa;
  border-radius: 50%;
  width: 3.125rem;
  height: 3.125rem;
}

.services .icon {
  font-size: 1.4rem;
  color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.services .box {
  height: 16rem;
  border-radius: 5px;
  border-bottom: 0.3125rem solid #5d89aa;
  padding: 0.625rem;
  margin-bottom: 1.875rem;
  background: #fff;
  transition: 0.2s ease-in-out;
}

.services .box:hover {
  background: #5d89aa;
  border-bottom: 0.625rem solid #282c34;
  filter: drop-shadow(3px 6px 9px #0c0c0c);
}

.services .box:hover .icon {
  color: black;
}

.services .box:hover .circle {
  background: blanchedalmond;
}

.services .box:hover p {
  color: #fff;
}



/* Project */

.project-wrapper {
  background: #585858;
  padding: 3rem 0;
  margin-top: 3rem;
}
.project-wrapper h1{
  color: #fff;
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-size: 2.5rem !important;
}

.project-img1 {
  width: 32rem;
  border: 1px solid #5DADEC;
  padding: 0 0.5rem;
}
.project-img2 {
  width: 27rem;
  border: 1px solid #5dadec;
  padding: 0 0.5rem;
}

.project-img-box1 {
  position: relative;
  margin: 1rem;
  margin-left: -1rem;
}


.project-img-box2 {
  position: relative;
  margin: 1rem;
  width: 100%;
  margin-left: 15rem;
}

.imga {
  display: flex ;
  
  flex-direction: row ;
}

/* Overflow of project */

.overflow1 {
  position: absolute;
  opacity: 0;
  background: #000000;
  width: 32rem;
  height: 15rem;
  top: 0;
  border: 1px solid ;

  cursor: pointer;

}

.overflow2 {
  position: absolute;
  opacity: 0;
  background: #000000;
  width: 27rem;
  height: 15rem;
  top: 0;
  border: 1px solid ;
  cursor: pointer;

}

 
.project-img-box1:hover .overflow1 {
  opacity: 60%;
  transition: 0.2s ease-in-out;
} 

.project-img-box2:hover .overflow2 {
  opacity: 60%;
  transition: 0.2s ease-in-out;
} 

.project-icon1 {
  position: absolute;
  opacity: 0;
  margin-left: -16rem;
  margin-top: 7rem;
  color: orange;
  font-size: 1.5rem;
}
.project-icon {
  position: absolute;
  opacity: 0;
  margin-left: -14rem;
  margin-top: 7rem;
  color: orange;
  font-size: 1.5rem;
}

.project-img-box1:hover .project-icon1 {
    opacity: 70%;
    transition: 0.3s ease-in-out;
    cursor: pointer;
} 

.project-img-box2:hover .project-icon {
  opacity: 70%;
  transition: 0.3s ease-in-out;
  cursor: pointer;
} 

/* Overflow popup */

.project-img-popup1{
  width: 50rem;
  padding: 0 0.5rem;

}

.project-img-popup2{
  width: 50rem;
  padding: 0 0.5rem;

}
.hyper-link {
  cursor: pointer;
  color: #3f7d96;

}
.hyper-link:hover {
  color: #ef4035;
}


@media(max-width: 468px)
{
  .popup-wrapper {
    height: 50%;
  }
  .project-img-box1 {
    width: 1rem;
    display: flexbox;
    flex-direction: row-reverse;
  }
  
  .project-img-box2 {
    width: 1rem;
    margin-left: 1rem;
    display: flexbox;
    flex-direction: row-reverse;
  }

.project-img1 {
  margin-left: 1.5rem;
  width: 18rem;
  height: 10rem;
  border: 1px solid #5DADEC;
  padding: 0 0.5rem;
}
.project-img2 {
  
  margin-top: 11rem;
  margin-left: 0rem !important;
  width: 18rem;
  border: 1px solid #5dadec;
  padding: 0 0.5rem;
}
.project-img-popup1{
  width: 80%;
}

.project-img-popup2{
  width: 80%;
}
} 

@media(max-width:768px){
  .project-img-popup{
    width: 80%;
  }
 
}

@media(max-height:570px){
  .project-img-popup{
    height: 50%;
  }
  
}


/* Certificate */

.certificate{
  background-image: url(bg.jpg);
  background-size: cover;
  background-color: #0c0c0c;
  text-align: center;
  padding-bottom: 4rem;
  margin-bottom: 0;
  width: 100%;
  margin-top: 3rem;
}

.certificate h1 {
  color: white;
  font-family: 'Times New Roman', Times, serif;
  font-weight: bold;
  padding: 3rem 0;
  text-transform: uppercase;
}

 .certificate-content{
  text-align: center;
  max-width: 53.125rem;
  margin: 0 auto 1rem auto;
  background: rgba(0,0,0,0.7);
} 

.my-carousel{
  color: #fff;
  background: transparent ;
  margin-top: 8%;
  width: 80%;
  margin-left: 4rem;
  margin-right: auto;
  padding-top: 6%;
  padding-bottom: 8%;
  padding-left: 5%;
  padding-right: 5%;
 
}

@media(max-width: 468px)
{
  .certificate-content {
    width: 22rem !important;
    height: 20rem !important;
  }

  .my-carousel{
    width: 11rem !important;
    height: 15rem !important;
  }

  .my-carousel img {
    width: 5px !important;
    height: 50px !important;
  }
  
  .carousel .slide img {
  width: 5rem !important;
  height: 10rem !important;
  }
  .my-carousel h3 {
     font-size: 15px !important;
  }

}

@media(max-height: 500px)
{
  
  .certificate-content {
    width: 22rem !important;
    height: 20rem !important;
  }

  .my-carousel{
    width: 11rem !important;
    height: 15rem !important;
  }
  
  .carousel .slide img {
  width: 5rem !important;
  height: 10rem !important;
  }
  .my-carousel h3 {
     font-size: 15px !important;
  }

}

.my-carousel h3 {
  color: orange;
  font-weight: 600;
  text-transform: uppercase;
}

.carousel .slide img {
  width: 40rem !important;
}

.carousel .slide {
  background: transparent !important;
  height: 100% !important;
}

.carousel-root {
  margin: auto !important;
  margin-top: 3% !important;
} 


/* Contact Form */

.contacts {
    background-color: #b6aeae;
    padding: 3rem 0;
    margin-top: 3rem;
}

.contacts h1 {
  color: #05a6e6;
  padding: 1.3rem 0;
  text-transform: uppercase;
}

.contacts p {
  color: #111111;
  font-size: 1.2rem;
}



.contacts input, .contacts textarea {
    width: 100%;
    box-sizing: border-box;
    background: none;
    resize: none;
    border: 0;
    border-bottom: 0.125rem solid #ddd2d2;
    border-radius: 0;
    outline: 0 !important;
    box-shadow: none !important;
    margin-bottom: 1rem;
    padding-left: 0 !important;
    
}

.contacts .container {
  padding: 4rem 0;
}

.contacts input:focus, .contacts textarea:focus {
  background: transparent;
  color: #f6f7f8;
  border-bottom: 0.125rem solid #ddd2d2;
}

.contacts textarea {
  height: 12.46rem !important;
 
}

.contact-btn {
  margin: 1rem 0 0 0;
  width: 45%;
  background: none;
}

.line {
  position: relative;
  top: -0.8rem;
  width: 0;
  height: 0.125rem;
  background: orange;
  display: inline-block;
}

input:focus+.line, textarea:focus+.line {
  width: 100%;
  transition: width 0.5s ease-in-out;
}

input.form-control {
  margin: -0.3rem 0;
}
textarea.form-control {
  margin: -0.3rem 0;
}

.success-message {
  font-weight: 900;
  color: green;
}



@media(max-width: 768px)
{

  .contacts {
    overflow-x: hidden;
  }

  .contacts .container {
    padding: 0 1rem;
  }

  .contacts p {
    padding: 0 0.4rem;
  }

  .contact-btn {
    width: 100%;
  }
}


/* Footer */

.footer {
  background: black;
  color: whitesmoke;
  padding: 3rem;
  height: 100%;
  width: 100%;
}

.footer a{
  text-decoration: none;
  cursor: pointer;
  color: whitesmoke;
  text-transform: capitalize;

}

.col a:hover {
  text-decoration: none;
  color: orangered;
}

.foicon {
  margin-left: 35rem;
  margin-top: -4rem !important;  

}
.foicon ul {
    position: absolute;
    top: 274rem;
    left: 50%;
    transform: translate(-50%, -50%);
    margin: 0%;
    padding: 0;
    display: flex;
}


@media(max-width: 768px)
{
  .footer{
    height: 18rem;
    width: 100%;
  }

  .foicon ul {
    top: 358rem;
  }
}

.foicon li {
  list-style: none;
}

.foicon a {
  position: relative;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 10px;
  border-radius: 50%;
  padding: 6px;
  box-sizing: border-box;
  text-decoration: none;
  box-shadow: 0 10px 10px rgba(0,0,0,0.5),
  inset 2px 2px 5px rgba(0,0,0,0.5),
  inset -3px -3px 7px rgba(255,255,255,0.05);
  background: linear-gradient(0deg, rgba(255,255,255,0.05), #131419);
}
.foicon a:hover {
  box-shadow: 0 2px 5px rgba(0,0,0,0.5);
}


.foicon ul a i {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(0deg, rgba(255,255,255,0.05), #131419);
  border-radius: 50%;
  line-height: calc(60% - 12px);
  font-size: 24px;
  color: #6d6c6c;
  transition: .5s;
}

.foicon ul li:nth-child(1) a:hover {
  color: #0e2c6d;
}

.foicon ul li:nth-child(2) a:hover {
  color: #166606;
}

.foicon ul li:nth-child(3) a:hover {
  color: #e4405f;
}

.foicon ul li:nth-child(4) a:hover {
  color: #25d366;
}




.social-list__item {
    margin-left: 5em;
}

